.hero {
    position: relative;
    background-image: image-set(
      url('../../img/hero-bg.avif') type("image/avif"),
      url('../../img/hero-bg.webp') type("image/webp"),
      url('../../img/hero-bg.jpg') type("image/jpg"),
    );
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 100vh;
    &:before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        display: block;
        background: rgba(0, 0, 0, 0.75);
        z-index: 1;
    }

    &__content {
        padding-top: 250px;
    }

    &__subtitle {
        position: relative;
        padding-left: 10px;
        color: #fff;

        z-index: 2;
        @include circle;
    }

    &__title {
        position: relative;
        margin-top: 10px;
        max-width: 536px;
        color: #fff;
        z-index: 2;
    }

    &__btns {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 250px;
        margin-top: 25px;
        z-index: 2;
    }

    &__portfolio-btn {
        display: block;
        margin-right: 45px;
        color: #fff;
    }

    &__hamburger {
        position: absolute;
        top: 20px;
        right: 30px;
        display: flex;
        align-items: center;
        width: 36px;
        height: 30px;
        cursor: pointer;
        z-index: 2;
        span {
            display: block;
            width: 100%;
            height: 2px;
            background: #fff;
            &::after {
                content: '';
                display: block;
                width: 100%;
                height: 2px;
                transform: translateY(-10px);
                background: #fff;
            }
            &::before {
                content: '';
                display: block;
                width: 100%;
                height: 2px;
                transform: translateY(8px);
                background: #fff;
            }
        }
    }
}
