.main-btn {
    height: 45px;
    line-height: 45px;
    padding: 0 25px;
    background: var(--main-color);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    border-radius: 5px;
    font-weight: 700;
    font-size: 14px;
    color: #000000;
    cursor: pointer;
}

.second-btn {
    font-weight: 700;
    font-size: 14px;
    color: #fff;
    background: none;
    border: none;
    cursor: pointer;
}