.price {
  padding-top: 60px;
  &__title {
    position: relative;
    text-align: center;
    @include circle;
    &:after {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
  &__wrapper {
    padding: 0 50px;
    margin-top: 60px;
    display: grid;
    grid-template-columns: repeat(2, 345px);
    grid-template-rows: repeat(3, 45px);
    grid-auto-rows: 45px;
    row-gap: 15px;
    justify-content: space-between;
  }

  &__item {
    position: relative;
    padding-left: 25px;
    &-inner {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &-title,
    &-price {
      font-weight: 700;
      font-size: 14px;
      line-height: 21px;
      color: #000000;
    }
    &-descr {
      font-weight: 500;
      font-size: 12px;
      line-height: 18px;
      color: #000000;
    }

    &:before {
      content: '';
      width: 15px;
      height: 15px;
      position: absolute;
      top: 3px;
      left: 0;
      background-color: var(--main-color);
      border-radius: 100%;
    }
  }
}
