.menu {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100vh;
    z-index: 10;
    visibility: hidden;
    opacity: 0;
    transition: 0.6s;
    &.active {
        left: 0;
        visibility: visible;
        opacity: 1;
    }
    &__block {
        position: relative;
        width: 345px;
        height: 100%;
        padding-top: 140px;
        padding-left: 40px;
        background: var(--main-color);
        transition: all 0.6s;
        z-index: 10;
    }

    &__close {
        position: absolute;
        top: 25px;
        right: 25px;
        cursor: pointer;
    }

    &__list {
        padding-left: 35px;
    }

    &__link {
        position: relative;
        font-weight: 700;
        font-size: 24px;
        line-height: 36px;
        a {
            color: #FFFFFF;
            text-decoration: none;
        }
        &:not(:last-child) {
            margin-bottom: 10px;
        }
        &:before {
            content: '';
            position: absolute;
            left: -35px;
            top: 10px;
            width: 15px;
            height: 15px;
            border-radius: 100%;
            border: 1px solid #000;
            transition: border-color 0.4s, background-color 0.4s;
        }
        &:hover {
            &:before {
                border-color: #fff;
                background-color: #fff;
            }
        }

    }

    &__social {
        position: absolute;
        bottom: 50px;
        left: 50%;
        transform: translateX(-50%);
        width: 90px;
        display: flex;
        align-items: center;
        justify-content: space-between;
      &-link {
        svg {
          width: 20px;
          height: 20px;
          fill: #fff;
        }
      }
    }

    &__overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #fff;
        opacity: 0.75;
    }
}
