.resume {
    padding-top: 120px;
    &__title {
        position: relative;
        text-align: center;
        @include circle;
        &:after {
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
    &__subtitle {
        margin-top: 15px;
        text-align: center;
    }

    &__wrapper {
        padding: 60px 65px 0 65px;
        display: grid;
        grid-template-columns: repeat(2, 425px);
        column-gap: 135px;
    }

    &__column {
        &-title {
            position: relative;
            padding-left: 35px;
            &::before {
                content: '';
                position: absolute;
                left: -7px;
                top: 50%;
                transform: translateY(-50%);
                width: 15px;
                height: 15px;
                border-radius: 100%;
                background-color: var(--main-color);
            }
        }

        ul {
            li {
                position: relative;
                &::after {
                    content: '';
                    position: absolute;
                    width: 1px;
                    height: calc(100% + 5px);
                    top: -5px;
                    left: 0;
                    background-color: #000;
                }
            }
        }
    }

    &__item {
        position: relative;
        &:before {
            content: '';
            height: 1px;
            width: 43px;
            position: absolute;
            top: 60px;
            background-color: #000;
        }
        &-head {
            position: relative;
            padding: 45px 0 0 100px;
        }
        &-location {
            margin-top: 5px;
            font-weight: 500;
            font-size: 12px;
            line-height: 18px;
            color: #000000;
        }
        &-body {
            padding-left: 43px;
            margin-top: 15px;
            font-weight: 500;
            font-size: 12px;
            line-height: 18px;
            color: #000000;
        }
        &-icon {
            position: absolute;
            top: 48px;
            left: 43px;
            @include circle;
            svg {
                width: 25px;
                height: 25px;
                position: relative;
                left: 17px;
                top: 9px;
            }
        }
    }
}
