.about {
    padding-top: 120px;

    .divider {
        margin-left: 0;
    }
    &__inner {
        display: grid;
        grid-template-columns: 400px 670px;
        grid-template-rows: minmax(245px) minmax(255px);
        grid-auto-rows: minmax(245px);
        column-gap: 70px;
        row-gap: 40px;
    }

    &__photo {
        grid-row: 1/3;
        height: 100%;
        border-radius: 5px;
    }
    &__img {
        position: relative;
        &:after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;border-radius: 5px;
            background-color: rgba(0,0,0,0.2);
        }
    }

    &__subtitle {
        position: relative;
        padding-left: 12px;
        padding-top: 1px;
        @include circle;
    }

    &__content {
        padding-top: 8px;
        .divider {
            margin-top: 10px;
        }
    }

    &__title {
        margin-top: 10px;
    }

    &__text {
        font-size: 14px;
        font-weight: 500;
        line-height: 21px;
        margin-top: 20px;
    }
}

.attainments {
    margin-top: 40px;
    display: grid;
    grid-template: repeat(2, 1fr) / repeat(2, 1fr);
    grid-auto-rows: 1fr;
    column-gap: 50px;
    row-gap: 20px;
    &__item {
        display: grid;
        grid-template-columns: 40px 200px;
        column-gap: 20px;
        &-icon {
            position: relative;
            @include circle;
            padding: 15px 0 0 15px;
            svg {
              width: 25px;
              height: 25px;
              fill: #000;
            }
            &:after {
                top: 18px;
            }
        }

        &-text {
            margin-top: 5px;
            font-weight: 500;
            font-size: 12px;
            line-height: 18px;
            color: #000000;
        }
    }
}
