.skills {
  padding-top: 120px;
  &__title,
  &__subtitle {
    text-align: center;
  }

  &__title {
    position: relative;
    @include circle;
    &:after {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  &__subtitle {
    margin-top: 15px;
  }

  &__wrapper {
    margin-top: 60px;
    display: grid;
    grid-template-columns: repeat(4, 270px);
    grid-template-rows: minmax(270px, auto) minamx(270px, auto);
    grid-auto-rows: minmax(270px, auto);
    gap: 13px;
  }

  &__item {
    padding: 30px 30px 25px 30px;
    border: 1px solid #000000;
    &-title {
      margin-top: 20px;
    }
    &-text {
      margin-top: 15px;
      font-weight: 500;
      font-size: 12px;
      line-height: 18px;
      color: #000000;
    }
  }
}
