.sidepanel {
    position: fixed;
    top: 230px;
    left: 50px;
    height: 340px;
    width: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    z-index: 3;

    &__list {
        display: flex;
        flex-direction: column-reverse;
    }

    &__item {
        &:not(:last-child) {
            margin-top: 15px;
        }
    }

    &__link {
      svg {
        width: 20px;
        height: 20px;
        fill: #fff;
      }
    }

    &__divider {
        width: 2px;
        height: 70px;
        background-color: #fff;
    }

    &__text {
        height: 150px;
        width: 25px;
        color: #fff;
        span {
            display: block;
            width: 150px;
            transform: rotate(-90deg) translate(-35%, -345%);
        }
    }
}
