.portfolio {
  padding-top: 120px;
  &__subtitle {
    margin-top: 15px;
  }
  &__title {
    position: relative;
    @include circle;
    &:after {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
  &__wrapper {
    margin-top: 60px;
    display: grid;
    grid-template-columns: repeat(4, 280px);
    grid-template-rows: repeat(2, 280px);
    grid-auto-rows: 280px;
    gap: 5px;
  }

  &__item {
    display: block;
    cursor: pointer;
    &--vertical {
      grid-row: 1/3;
      grid-column: 4/5;
    }
    &--horizontal {
      width: 100%;
      height: 100%;
      grid-column: 2/4;
      grid-row: 2/3;
    }
  }
}
